import { Container } from '@mui/material'
import React, { Component } from 'react'
import Dashboard from './Dashboard'

export class Welcome extends Component {
    render() {
        return (
            <>
            <Dashboard/>
            <Container>
                 <div className=" row">
                  <div className="col p-0 mr-1" >
                  <div class="jumbotron" style={{height:"550px"}}>
                      <h1 class="display-4">Hello, Welcome!</h1>
                        <p class="lead">This is a simple hero unit, a simple jumbotron-style component for calling extra attention to featured content or information.</p>
                        <hr class="my-4"/>
                        <p>It uses utility classes for typography and spacing to space content out within the larger container.</p>
                        <p class="lead">
                          <a class="btn btn-primary btn-lg" href="#" role="button">Learn more</a>
                        </p>
                  </div>
                  </div>
                  <div className="col p-0ml-1" style={{backgroundImage:"url(images/dashboard3.jpg)", height:"550px",backgroundRepeat:"no-repeat",backgroundSize:"100% 100%"}}>
                  </div>
                </div>
                </Container>
            </>
        )
    }
}

export default Welcome
