import React, { Component } from 'react';
import {Stack,Divider, Container} from '@mui/material';
import {Link} from 'react-router-dom'
import { Box } from '@mui/system';

export class Dashboard extends Component {
    render() {
        return (
            <>
            <Container>
              
            <nav className="navbar navbar-expand-lg navbar-light bg-primary row  mb-3" >
                <Link to="/welcome" className="navbar-brand col-8 mr-5" >To Do App</Link>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
                    <div className="navbar-nav ml-5 col-4">
                        <Link  to="/register" style={{textDecoration:"none"}} className="nav-item nav-link text-white" >Register <span className="sr-only">(current)</span></Link>
                        <Link  to="/login" style={{textDecoration:"none"}} className="nav-item nav-link text-white" >Login</Link>
      
                    </div>
                </div>
              </nav>
              <main >
                
               
                
              </main>
              </Container>
           
            </>
        )
    }
}

export default Dashboard
