import React, { Component } from 'react';
import { Container } from '@mui/material'
import DisplayTask from './DisplayTask';
import { Box } from '@mui/system';


// const regForName=RegExp(/^[A-Za-z]{5,50}$/);
const regForPriority=RegExp(/^[1-5]{1}$/)

export class AddTask extends Component {
    constructor(props){
        super(props)
        this.state={title:null,priority:null,todo:[],errors:{title:'',priority:''}}
        // this.sortTasks=this.sortTasks.bind(this);
    }

    /* componentDidMount(){
        this.setState({todo:JSON.parse(localStorage.getItem('mytask'))})
        console.log(JSON.parse(localStorage.getItem('mytask')))
        
    } */

    handler=(event)=>{
        const {name,value}=event.target;
        let errors=this.state.errors;
        switch(name){
            // case 'title':
            //     errors.title=regForName.test(value)?'':'Title is not valid';
            //     break;
            case 'priority':
                errors.priority=regForPriority.test(value)?'':'Enter Priority between 1-5 5-Highest 1-Lowest!!';
                break;
            default :
                break;
        }
        this.setState({errors,[name]:value},()=>{
            // console.log(errors)
        })

    }

    addTask=(event)=>{
        event.preventDefault();
        const {name,value}=event.target;
        // console.log({[name]:value})
        this.setState({[name]:value})
        // console.log(this.state)
        let {title,priority}=this.state

        if(this.validate(this.state.errors) && title!==null && priority!==null)
        {
            if(localStorage.getItem('mytask') !== null)
            {
                let arr=JSON.parse(localStorage.getItem('mytask'))
                console.log(arr)
                    if(arr.includes(title)){
                        alert("Task already Added!!")
                    }
                    else{
                        
                        alert('Task Added!!')
                        arr.push({title,priority,complete:false})
                        localStorage.setItem('mytask',JSON.stringify(arr))
                        
                    }
                
            }  
       else{
       let arr=[{title,priority,complete:false}]
       alert('Task Added!!')
         arr.push()
         localStorage.setItem('mytask',JSON.stringify(arr))
        // return this.props.history.push("/home")
       
        
           } 
        }
        else{
           return alert("Enter Correct Title and Priority!!")
        }
        document.getElementById("title").value=null;
        document.getElementById("priority").value=null;
        // this.setState({todo:JSON.parse(localStorage.getItem("mytask"))})
        
    }



    validate=(errors)=>{
        let valid=true;
        Object.values(errors).forEach((val)=> val.length >0 && (valid=false));
        return valid;
    }

     refreshPage=()=> {
        window.location.reload(false);
      }


   /*  sortTasks=()=>{
        if(this.state.todo!==null){
            this.setState(prevState => {
                this.state.todo.sort((a, b) => (b.priority - a.priority))
            });
            console.log(this.state.todo)
        }
       
  
    } */
    render() {
        const {errors}=this.state;
        return (
            <>
               <Container>
                   <Box height="auto" width="500px" margin="auto" style={{backgroundColor:"#0063B2FF",boxShadow:" 0 3px 10px rgb(0 0 0 / 0.2)",borderRadius:"10px"}} className=" p-3 mt-3 mb-3">
                       <h3 className="text-center text-white">Add Task</h3>
            <form onSubmit={this.addTask}   >
            <div className="form-group">
                    <label className="text-white">Title</label>
                    <input type="text" name="title" id="title" className="form-control" onChange={this.handler}  /* value={inputValue} *//><br/>
      {/* {errors.title.length>0 && <span style={{color:'red'}}>{errors.title}</span>} */}
                </div>
                <div class="form-group ">
                    <label  className="text-white">Priority</label>
                    <input type="number" name="priority" id="priority" className="form-control" onChange={this.handler}  /* value={inputValue} *//><br/>
      {errors.priority.length>0 && <span className="text-warning" >{errors.priority}</span>}
                </div>
                <div className="text-center">
                <input type="submit" value="Submit" style={{backgroundColor:"#9CC3D5FF",color:"#0063B2FF"}} className="btn" onClick={this.refreshPage} /></div>
            </form>
            </Box>
            <DisplayTask/>
            

            </Container>
            </>
        )
    }
}

export default AddTask
