import React, { useState,useEffect } from 'react';
import {ToggleButton} from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';
import DoneIcon from '@mui/icons-material/Done';
import ReactHtmlParser from 'react-html-parser';
import '../css/style.css'
import { bgcolor } from '@mui/system';


export default function DisplayTask() {
    const [todo,setTodo]=useState(JSON.parse(localStorage.getItem('mytask')));
   

    let sortedArr=[];
    
    useEffect(()=>{
        // setTodo({todo:JSON.parse(localStorage.getItem('mytask'))})
        // event.preventDefault();
        if(localStorage.getItem('mytask') !== null && todo!== undefined){
            setTodo(prevState => {
                todo.sort((a, b) => (b.priority - a.priority))
                console.log(todo)
                localStorage.setItem('mytask',JSON.stringify(todo)) 
              })
              
           /*  setTodo(prevState => {
                todo.sort((a, b) => (b.priority - a.priority))
            });
            console.log(todo)
            localStorage.setItem('mytask',JSON.stringify(todo)) 
           console.log(JSON.parse(localStorage.getItem("mytask"))) */
          
           
        }
        
    },[])
    sortedArr=JSON.parse(localStorage.getItem("mytask"))
    console.log(sortedArr)
    

     const deleteTask=(id)=>{
        let filteredArray = sortedArr.filter((item,key) => key !== id)
        setTodo({todo: filteredArray});
        console.log(filteredArray)
        localStorage.setItem('mytask',JSON.stringify(filteredArray))
    }

    const complete=(id)=>{       
            // if(sortedArr[id].complete=== false){
                sortedArr[id].title = `<strike>${sortedArr[id].title}</strike>`;
                // sortedArr[id].complete=true;
                setTodo({todo:sortedArr});
                localStorage.setItem('mytask', JSON.stringify(sortedArr));
            // }
            // else{
            //     sortedArr[id].title = `${sortedArr[id].title}`; 
            //     sortedArr[id].complete=false;
            //     setTodo({todo:sortedArr});
            //     localStorage.setItem('mytask', JSON.stringify(sortedArr));
               
            // }
       
            
    }
    

    /* const sortTasks=()=>{
        setTodo(prevState => {
            todo.sort((a, b) => (b.priority - a.priority))
        });

        console.log(todo)
        localStorage.setItem('mytask',JSON.stringify(todo)) 
       console.log(JSON.parse(localStorage.getItem("mytask")))
       return 1
    }  */
   
        return (
            <>
          
            {console.log(sortedArr)}
            {(localStorage.getItem('mytask') !== null )?
            <div class="table-wrapper-scroll-y my-custom-scrollbar">
               <table className="table table-bordered table-striped table-hover" style={{fontSize:"20px"}}>
                   <thead>
                       <tr>
                           <th>Title</th>
                           <th>Priority</th>
                           <th>Done|Delete</th>
                       </tr>
                   </thead>
                   <tbody>
                       
                       {sortedArr.map((task,id)=>
                       
                        <tr>
                        <td className="col-8" id="title">{ReactHtmlParser(task.title)}</td>
                        <td className="col-2" >{task.priority}</td>

                        <td className="col-2"> 
                            <ToggleButton onClick={() => {complete(id)}} style={{border:'2px solid green'}} ><DoneIcon style={{color:'green'}}/></ToggleButton>
                            &nbsp;
                            <ToggleButton className="deletetask" onClick={() => {deleteTask(id)}} style={{border:"2px solid red"}} ><CloseIcon style={{color:'red'}} /></ToggleButton>
                        </td>
                        </tr>
    )}

                       
                   </tbody>
               </table></div> :
               ''}
            </>
        )
    
}


