import React, { Component } from 'react'


export class Logout extends Component {
    constructor(props){
        super(props)
        this.state={checkpass:JSON.parse(localStorage.getItem('myuserlogin'))}
    }
    componentDidMount(){
        if(localStorage.getItem('myuserlogin') !== null){
            if(window.confirm("Do You want to Logout?")){
                this.setState({checkpass:null})
                console.log(this.state.checkpass)
                localStorage.removeItem('myuserlogin',JSON.stringify(this.state.checkpass))
                return this.props.history.push("/")
           
            }
            else{
                return this.props.history.push("/home")
            }
        }else{
            return this.props.history.push("/")
        }

    }
    render() {
        return (
           <>
           </>
        )
    }
}

export default Logout
