import { Container } from '@mui/material'
import React, { Component } from 'react'
import AddTask from './AddTask'
import LogoutIcon from '@mui/icons-material/Logout';
import {ToggleButton} from '@mui/material';
import Logout from './Logout';
import { Link } from 'react-router-dom';
import { Box } from '@mui/system';


export class Home extends Component {
    constructor(props){
        super(props)
        this.state={userLogin:JSON.parse(localStorage.getItem('myuserlogin'))}
    }
   
    render() {
        return (
            <>
            <Box style={{backgroundColor:"#9CC3D5FF",width:"100%"}} className="p-0 m-0">
                <div className="row">
                <div className="col-11">
                <h1 className="text-left ml-5">Todo List</h1>
                {/* <h4 >Welcome  {this.state.userLogin!==null ?this.state.userLogin.map(user=>user.email):''} !!!</h4> */}
                </div>
                <div className="col-1">
                    <Link to="/logout"><ToggleButton className="border-primary"><LogoutIcon className="text-primary"/></ToggleButton></Link>
                </div>
                </div>
                <AddTask/>
            </Box>
            </>
        )
    }
}

export default Home
