import React, { Component } from 'react';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import { Container,Button ,FormControlLabel,Checkbox} from '@mui/material';
import Dashboard from './Dashboard';



const regForEmail=RegExp(/^[^\s@]+@[^\s@]+\.[^\s@]+$/);

export class Login extends Component {
    constructor(props){
        super(props)
        this.state={arr:[],userData:[], email:null,password:null,
                    errors:{
                        email:'',password:''
                    }
        }
    }
    componentDidMount(){
        this.setState({userData:JSON.parse(localStorage.getItem('myuser'))})
    }
    forPassword=(value)=>{
        const upper=RegExp(/^(?=.*[A-Z]).*$/);
        const lower=RegExp(/^(?=.*[a-z]).*$/);
        const special=RegExp(/^(?=.*[@$!%*?&]).*$/);
        const num=RegExp(/^(?=.*[0-9]).*$/);
        if(!upper.test(value)){
            return 'Password must contain atleast 1 Uppercase character';}
        else if(!lower.test(value)){
            return 'Password must contain atleast 1 Lowercase character';}
        else if(!special.test(value)){
            return 'Password must contain atleast 1 Special character';}
        // else if(value!== this.state.fname && value!== this.state.lname){
            // return 'Password cannot contain users first name or last name';}
            else if(!num.test(value)){
                return 'Password must contain atleast 1 Number';}
            else if(value.length<8){
                return 'Password must contain minimum 8 characters';}
        else{ return '';}
    };
    handler=(event)=>{
        const {name,value}=event.target;
        let errors=this.state.errors;
        switch(name){
            case 'email':
                errors.email=regForEmail.test(value)?'':'Email is not valid';
                break;
            case 'password':
                errors.password=this.forPassword(value);
                console.log(errors.password)
                break;
            default :
                break;
        }
        this.setState({errors,[name]:value},()=>{
            console.log(errors)
        })
    }
    checkLogin=(event)=>{
        // alert("hello")
        event.preventDefault();
        const {name,value}=event.target;
        this.setState({[name]:value})
        
        let {password,email} = this.state;

        if(this.validate(this.state.errors))
        {this.state.userData.map(user=>
            {
                if(user.email.includes(email) && user.password.includes(password)){
                    if(localStorage.getItem('myuserlogin') !== null){
                         alert("Another User already logged in!!")
                            }  
                    else{
                    let arr=[{email,password}]
                    alert('Logged In Successfully!')
                      arr.push()
                      localStorage.setItem('myuserlogin',JSON.stringify(arr))
                     return this.props.history.push("/home")
                     
                        }
                }
                else{
                alert("User Not Found!Please Register yourself!!")
                return this.props.history.push("/register")
                // document.getElementById("email").value=null;
                // document.getElementById("password").value=null;
                }
            }
        )
       
        }
        // else{
        //    return alert("Enter Correct Email and Password!!")
        // }
        document.getElementById("email").value=null;
        document.getElementById("password").value=null;
    }
    validate=(errors)=>{
        let valid=true;
        Object.values(errors).forEach((val)=> val.length >0 && (valid=false));
        return valid;
    }
   
    render() {
        const {errors}=this.state;
        return (
            <>
            <Dashboard/>
            <Container style={{backgroundColor:"Background"}} className="p-5">
            <Box style={{backgroundColor:"HighlightText",boxShadow:" 0 3px 10px rgb(0 0 0 / 0.2)",borderRadius:"10px"}} textAlign="center" height="400px" width="350px" margin="auto" marginTop="20px">
                <h1>Login</h1>
                 <Box bgcolor=""
       padding="3"
       component="form"
       noValidate
       autoComplete="off">

      <div><TextField margin="dense" type="text" name="email" id="email" label="Email" variant="outlined" required onChange={this.handler} onMouseOver={this.handler}/><br/>
      {errors.email.length>0 && <span style={{color:'red'}}>{errors.email}</span>}</div>

      <div><TextField margin="dense" type="password" name="password" id="password" label="Password" variant="outlined" required onChange={this.handler} onMouseOver={this.handler}/><br/>
      {errors.password.length>0 && <span style={{color:'red'}}>{errors.password}</span>}</div>

      <div><FormControlLabel control={<Checkbox defaultChecked />} label="Remember me" /></div>

      <div>
              <Button margin="dense" variant="contained" color="success" type="submit" onClick={this.checkLogin}>Login</Button>
        </div>

    </Box>  
    </Box>
    </Container>
            </>
        )
    }
}

export default Login
