import React, { Component } from 'react';
import {Link} from 'react-router-dom'
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import { Container,Button ,FormControlLabel,Checkbox} from '@mui/material';
import Dashboard from './Dashboard';


const regForName=RegExp(/^[A-Za-z]{2,50}$/);
const regForEmail=RegExp(/^[^\s@]+@[^\s@]+\.[^\s@]+$/);
const regForUsername=RegExp(/^[A-Za-z0-9]{5,50}$/)


export class Register extends Component {
    constructor(props){
        super(props)
        this.state={arr:[],userData:[],fname:null,lname:null,username:null, email:null,password:null,con_password:null,
                    errors:{
                        fname:'',lname:'',username:'', email:'',password:'',con_password:''
                    }
        }
    }
    componentDidMount(){
        this.setState({userData:JSON.parse(localStorage.getItem('myuser'))})
    }
    forPassword=(value)=>{
        const upper=RegExp(/^(?=.*[A-Z]).*$/);
        const lower=RegExp(/^(?=.*[a-z]).*$/);
        const special=RegExp(/^(?=.*[@$!%*?&]).*$/);
        const num=RegExp(/^(?=.*[0-9]).*$/);
        if(!upper.test(value)){
            return 'Password must contain atleast 1 Uppercase character';}
        else if(!lower.test(value)){
            return 'Password must contain atleast 1 Lowercase character';}
        else if(!special.test(value)){
            return 'Password must contain atleast 1 Special character';}
        // else if(value!== this.state.fname && value!== this.state.lname){
            // return 'Password cannot contain users first name or last name';}
            else if(!num.test(value)){
                return 'Password must contain atleast 1 Number';}
            else if(value.length<8){
                return 'Password must contain minimum 8 characters';}
        else{ return '';}
    };
    handler=(event)=>{
        const {name,value}=event.target;
        let errors=this.state.errors;
        switch(name){
            case 'fname':
                errors.fname=regForName.test(value)?'':'Enter Valid name!';
                break;
            case 'lname':
                errors.lname=regForName.test(value)?'':'Enter Valid name!';
                break;
            case 'username':
                errors.username=regForUsername.test(value)?'':'Enter Valid Username!';
                break;
            case 'email':
                errors.email=regForEmail.test(value)?'':'Email is not valid';
                break;
            case 'password':
                errors.password=this.forPassword(value);
                console.log(errors.password)
                break;
            case 'con_password':
                errors.con_password=this.state.password===(value)?'':'Password does not match';
                 console.log(errors.password)
                break;
            default :
                break;
        }
        this.setState({errors,[name]:value},()=>{
            console.log(errors)
        })
    }
    checkLogin=(event)=>{
        // alert("hello")
        event.preventDefault();
        const {name,value}=event.target;
        this.setState({[name]:value})
        
        let {con_password,password,email,username,lname,fname} = this.state;

        if(this.validate(this.state.errors))
       
            {
                
                    if(localStorage.getItem('myuser') !== null){
                        let arr=JSON.parse(localStorage.getItem('myuser'))
                        console.log(arr)
                        this.state.userData.map(user=>
                        {if( user.email.includes(email) && user.password.includes(password))
                                alert(" User already Registered!!")


                        else{
                            
                            alert('Registered In Successfully!')
                            arr.push({con_password,password,email,username,lname,fname})
                            localStorage.setItem('myuser',JSON.stringify(arr)) 
                            return this.props.history.push("/login")
                     
                        }
                        })
                        
       
        
                            }  
                    else{
                    let arr=[{con_password,password,email,username,lname,fname}]
                    alert('Registered In Successfully!')
                      arr.push()
                      localStorage.setItem('myuser',JSON.stringify(arr)) 
                     return this.props.history.push("/login")
                     
                        }
                }
                /* else{
                alert("User Not Found!")
                document.getElementById("email").value=null;
                document.getElementById("password").value=null;
                } */
            
        
        else{
           return alert("Enter Correct Email and Password!!")
        }
        document.getElementById("email").value=null;
        document.getElementById("password").value=null;
    }
    validate=(errors)=>{
        let valid=true;
        Object.values(errors).forEach((val)=> val.length >0 && (valid=false));
        return valid;
    }
    render() {
        
        const {errors}=this.state;
        return (
            <>
                <Dashboard/>
                <Container style={{backgroundColor:"Background"}} className="p-3">
                <Box style={{backgroundColor:"HighlightText",boxShadow:" 0 3px 10px rgb(0 0 0 / 0.2)",borderRadius:"10px"}} className="" textAlign="center" height="auto" width="350px" padding="10px" margin="auto" marginTop="20px">
                <h1>Register</h1>
                 <Box     padding="3"
       component="form"
       noValidate
       autoComplete="off" >

      <div><TextField margin="dense" type="text" name="fname" id="fname" label="First Name" variant="outlined" required onChange={this.handler} onMouseOver={this.handler}/><br/>
      {errors.fname.length>0 && <span style={{color:'red'}}>{errors.fname}</span>}</div>

      <div><TextField margin="dense" type="text" name="lname" id="lname" label="Last Name" variant="outlined" required onChange={this.handler} onMouseOver={this.handler}/><br/>
      {errors.lname.length>0 && <span style={{color:'red'}}>{errors.lname}</span>}</div>

      <div><TextField margin="dense" type="text" name="username" id="username" label="Username" variant="outlined" required onChange={this.handler} onMouseOver={this.handler}/><br/>
      {errors.username.length>0 && <span style={{color:'red'}}>{errors.username}</span>}</div>

      <div><TextField margin="dense" type="text" name="email" id="email" label="Email" variant="outlined" required onChange={this.handler} onMouseOver={this.handler}/><br/>
      {errors.email.length>0 && <span style={{color:'red'}}>{errors.email}</span>}</div>

      <div><TextField margin="dense" type="password" name="password" id="password" label="Password" variant="outlined" required onChange={this.handler} onMouseOver={this.handler}/><br/>
      {errors.password.length>0 && <span style={{color:'red'}}>{errors.password}</span>}</div>

      <div><TextField margin="dense" type="password" name="con_password" id="con_password" label="Confirm Password" variant="outlined" required onChange={this.handler} onMouseOver={this.handler}/><br/>
      {errors.con_password.length>0 && <span style={{color:'red'}}>{errors.con_password}</span>}</div>

      <div><FormControlLabel control={<Checkbox defaultChecked />} label="Remember me" /></div>

      <div>
              <Button variant="contained" color="success" type="submit" onClick={this.checkLogin}>Register</Button>
        </div>

    </Box>  
    </Box>
    </Container>
        </>
        )
    }
}

export default Register
